import React, { useState } from "react";
import { Flex, Heading, Stack } from "@chakra-ui/layout";
import { Image } from "@chakra-ui/image";
import { Button } from "@chakra-ui/button";
import { Box } from "@chakra-ui/layout";
import { Text, List, ListItem } from "@chakra-ui/react";
import { Grid } from "@chakra-ui/react";
import './index.css';

const App = () => {
  const [gamestart, setGameStarted] = useState(false);
  const [selectedNumber, setSelectedNumber] = useState();
  const [dice, setDice] = useState(1);
  const [error, setError] = useState();
  const [score, setScore] = useState(0);

  const number = [1, 2, 3, 4, 5, 6];

  const startGame = () => {
    setGameStarted(true);
  };

  const onBtnClicked = () => {
    setGameStarted(false);
  };

  console.log(selectedNumber);
  const onNumberClicked = (value) => {
    setSelectedNumber(value);
    setError(null);
  };

  const genRandomNum = () => {
    if (selectedNumber) {
      const genNumber = Math.ceil(Math.random() * 6);
      setDice(genNumber);
      if (selectedNumber === genNumber) {
        setScore((prev) => prev + genNumber);
      } else {
        setScore((prev) => prev - 2);
      }
    } else {
      setError("Please select Number first!");
    }
  };


  return (
    <Grid
      templateColumns="1fr"
      gap={4}
      padding={4}
      className="container"
     >
      {gamestart ? (
        <>
        <Stack className="container">
          <Heading
            as="h1"
            fontSize="6xl"
            marginBottom="8"
            color={error ? "red" : "black"}
          >
            {error ? error : "Select Number"}
          </Heading>

          <Flex pb="10">
            {" "}
            {number.map((value) => (
              <Flex
                className="numbers"
                bg={selectedNumber === value ? "green" : "black"}
                key={value}
                onClick={() => onNumberClicked(value)}
                // _hover={{ bg: "grey" }}
              >
                {value}
              </Flex>
            ))}
          </Flex>

          <Box h="150px" width="150px" onClick={genRandomNum}>
            {" "}
            <Image src={`/dice/dice${dice}.png`} />
          </Box>
          <Text as="p" fontSize="xl">
            Click on the dice roll
          </Text>
          <Text className="score" color={score > 0 ? "green" : "red"}>
            {score}
          </Text>

          <Text  fontSize="5xl" fontWeight="bold">
            Total Score
          </Text>
          <Flex>
            <Button 
            className="btn"
             
              onClick={() => setScore(0)}
            
            >
              Reset Score
            </Button>
            <Button
              marginLeft={5}
            
              onClick={onBtnClicked}
            >
              Back
            </Button>
          </Flex>
        </Stack>

        <Stack maxW="900px" margin="auto">
          <Heading as="h2">Game Rules:-</Heading>
          <List>
            <ListItem>Select any number </ListItem>
            <ListItem>click on the dice image to roll </ListItem>
            <ListItem>
              Select number is equal to obtained dice result then you will get
              same point of dice{" "}
            </ListItem>
            <ListItem>
              If you are wrong Score will be deducted by 2 points{" "}
            </ListItem>
          </List>
        </Stack>
      </>
      ) : (
        <Flex className="flex" >
        <Image width="50%" src="/dices.png" />
        <Stack>
          <Heading as="h1">The Dice Game</Heading>
          <Button className="btn1"
    
            onClick={startGame}
          >
            Start Game
          </Button>
        </Stack>
      </Flex>
    )}
      </Grid>
   
  );
};

export default App;
